import axios from "axios";
import { GET_ERRORS, GET_PROJECTS, GET_PROJECT, DELETE_PROJECT } from "./types";

export const createProject = (project, history) => async dispatch => { //实际应用中，Reducer 函数不用像上面这样手动调用，
    //store.dispatch方法会触发 Reducer 的自动执行。
    try {
        const res = await axios.post("/api/project", project);
        history.push("/dashboard")
        dispatch({
            type:GET_ERRORS,
            payload:{}
        })
    } catch (error) {
        dispatch({
            type:GET_ERRORS,
            payload:error.response.data
        })
    }
}

export const getProjects = () => async dispatch => {
    const res = await axios.get("/api/project/all");
    dispatch({
        type: GET_PROJECTS,
        payload: res.data
    });
}

export const getProject = (id, history) => async dispatch => {
    try {
        const res = await axios.get(`/api/project/${id}`);
        dispatch({
            type: GET_PROJECT,
            payload: res.data
        });
    } catch (error) {
        history.push("/dashboard")
    }
}

export const deleteProject = id => async dispatch => {
    if (window.confirm("Are you sure? This will delete the project and all the data related to it.")){
        await axios.delete(`/api/project/delete/${id}`)
        dispatch({
            type: DELETE_PROJECT,
            payload: id
        });
    }
}